import React, { Component } from "react";
import {
  Toast,
  WhiteSpace,
  WingBlank,
  Button,
  Picker,
  List,
} from "antd-mobile";

import "./index.scss";

export default class Standard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          label: "2013",
          value: "2013",
        },
        {
          label: "2014",
          value: "2014",
        },
      ],
      cols: 1,
      asyncValue: [],
      LooseArr: [
        {
          todoList: [
            {
              code: "",
              title: "",
              num: 0,
              Examples: "********",
            },
            {
              code: "",
              title: "",
              num: 9,
              Examples: "*******1",
            },
            {
              code: "",
              title: "",
              num: 99,
              Examples: "******01",
            },
            {
              code: "",
              title: "",
              num: 99,
              Examples: "********",
            },
            {
              code: "",
              title: "",
              num: 999,
              Examples: "********",
            },
            {
              code: "",
              title: "",
              num: 999,
              Examples: "*****001",
            },
            {
              code: "",
              title: "",
              num: 4999,
              Examples: "*****001",
            },
          ],
          dealCnt: "", //
          number: "",
          endnumber: "", //
          dealPrice: "",
          cntDesc: "1", //
          tag: "",
          Examples: "********",
        },
      ],
    };
    this.selectChange = this.selectChange.bind(this);
    this.MyLoose_body = React.createRef();
  }
  state = {};
  componentDidMount() {
    if (sessionStorage.getItem("BIAOLIAN_ARR")) {
      // 
      this.setState({
        LooseArr: JSON.parse(sessionStorage.getItem("BIAOLIAN_ARR")),
      });
    }
  }
  inputOnFocus = () => {
    setTimeout(() => {
      window.scrollTo(0, this.MyLoose_body.current.offsetHeight);
    }, 300);
  };
  isPositiveInteger = (s) => {
    //
    if (s.length > 18) {
      return false;
    }
    var re = /^\+?[1-9]\d*$/;
    return re.test(s);
  };
  selectChange = (ev, item1, key) => {
    console.log(ev.target.value);
    console.log(item1);

    let LooseArr = [...this.state.LooseArr];
    let _this = this;
    console.log(LooseArr[key]);

    LooseArr.map((item, index, arr) => {
      if (key == index) {
        item.tag = ev.target.value;
        item.dealCnt = _this.changeDOM(ev.target.value, arr[key]).num;
        if (item.tag == "") {
          item.dealCnt = "";
          item.endnumber = "";
        }
        if (item.dealCnt && item.number) {
          item.endnumber = _this.SETNUmber(
            item.number,
            item.dealCnt,
            "1",
            item
          );
          // item.endnumber =
          //   Number(item.dealCnt) +
          //   Number(this.SETNUmber(item.number, item.dealCnt, "1", item));
        }
        item.Examples = _this.changeDOM(ev.target.value, arr[key]).Examples;
      }
    });

    this.setState({
      LooseArr: LooseArr,
    });

    console.log(this.state.LooseArr);

    sessionStorage.setItem("BIAOLIAN_ARR", JSON.stringify(LooseArr));
  };
  changeDOM = (value, arr) => {
    var obj = arr.todoList.find(function (key) {
      return key.title === value;
    });
    return obj;
  };
  delte = (item, key) => {
    console.log(key);
    var LooseArr = [...this.state.LooseArr];
    //
    LooseArr.splice(key, 1);
    this.setState({
      LooseArr: LooseArr,
    });
    sessionStorage.setItem("BIAOLIAN_ARR", JSON.stringify(LooseArr));
  };

  SETNUmber = (value, addNumber, num, obj) => {
    let newstr,
      ccccccc,
      bu0 = "";
    let [setstr, setstrLength] = [
      value.replace(/[^0-9]/gi, ""),
      value.replace(/[^0-9]/gi, "").length,
    ];
    let [AddSetstr, AddSetstrLength] = [
      `${Number(setstr) + Number(addNumber)}`,
      `${Number(setstr) + Number(addNumber)}`.length,
    ];
    if (value.indexOf(setstr) == -1) {
      Toast.info("", 2);
      return;
    }
    // log(value.indexOf(setstr))

    if (AddSetstrLength != setstrLength) {
      //0
      let bu0Cha = Number(setstrLength) - Number(AddSetstrLength);
      for (var i = 0; i < bu0Cha; i++) {
        bu0 += "0";
      }
      newstr = `${bu0}${AddSetstr}`;
    } else {
      newstr = `${AddSetstr}`;
    }
    ccccccc = value.replace(setstr, newstr);

    if (num == 1) {
      return ccccccc;
    } else {
      switch (obj.tag) {
        case "":
          if (setstr.substring(setstr.length - 1) != "1") {
            return false;
          } else {
            return true;
          }
          break;
        case "":
          if (setstr.substring(setstr.length - 2) != "01") {
            return false;
          } else {
            return true;
          }
          break;
        case "":
          if (setstr.substring(setstr.length - 3) != "001") {
            return false;
          } else {
            return true;
          }
          break;
        case "":
          if (setstr.substring(setstr.length - 3) != "001") {
            return false;
          } else {
            return true;
          }
          break;
        default:
          return true;
          break;
      }
    }
  };
  hanChange = (ev, index, item1) => {
    console.log(item1);
    console.log(ev.target.value);
    let _this = this;
    const LooseArr = [...this.state.LooseArr]; //
    // this.setState({
    //   LooseArr: LooseArr.map((item, key) =>
    //     key == index
    //       ? { ...item, number: ev.target.value, endnumber: 100 }
    //       : item
    //   ),
    // });
    console.log(LooseArr);

    LooseArr.map((item, key) => {
      if (key == index) {
        item.number = ev.target.value;
        if (ev.target.value && item.dealCnt) {
          item.endnumber = _this.SETNUmber(
            ev.target.value,
            item.dealCnt,
            "1",
            item
          );
        } else {
          item.endnumber = "";
        }
      }
    });

    this.setState({
      LooseArr: LooseArr,
    });

    sessionStorage.setItem("BIAOLIAN_ARR", JSON.stringify(LooseArr));
  };
  hanpriceChange = (ev, index) => {
    const LooseArr = [...this.state.LooseArr]; //

    LooseArr.map((item, key) => {
      if (key == index) {
        item.dealPrice = ev.target.value;
      }
    });
    this.setState({
      LooseArr: LooseArr,
    });

    // this.setState({
    //   LooseArr: LooseArr.map((item, key) =>
    //     key == index ? { ...item, dealPrice: ev.target.value } : item
    //   ),
    // });

    sessionStorage.setItem("BIAOLIAN_ARR", JSON.stringify(LooseArr));
  };
  hancntDescChange = (ev, index) => {
    const LooseArr = [...this.state.LooseArr]; //

    LooseArr.map((item, key) => {
      if (key == index) {
        item.cntDesc = ev.target.value;
      }
    });
    this.setState({
      LooseArr: LooseArr,
    });
    sessionStorage.setItem("BIAOLIAN_ARR", JSON.stringify(LooseArr));
  };
  Myoption = (item) => {
    console.log(item);
  };
  setBuyingNumber = (ischeck) => {
    console.log(this.props.userName);
    let maxNumber = 0;
    if (this.props.userName == "") {
      maxNumber = Number(1);
    } else {
      maxNumber = Number(3);
    }
    if (
      ischeck.length < maxNumber ||
      ischeck.length > 20 ||
      /[\u4E00-\u9FA5]/i.test(ischeck)
    ) {
      return false;
    } else {
      return true;
    }
  };
  add() {
    let utitle = this.props.utitle;
    let LooseObj = this.state.LooseArr[this.state.LooseArr.length - 1];
    var priceReg = /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/;

    for (let key in LooseObj) {
      if (!LooseObj[key]) {
        Toast.info(`${utitle}`, 2);
        return;
      }
    }

    console.log(LooseObj);
    // console.log(
    //   this.SETNUmber(LooseObj.number, LooseObj.dealCnt, "2", LooseObj)
    // );
    if (!this.SETNUmber(LooseObj.number, LooseObj.dealCnt, "2", LooseObj)) {
      Toast.info("", 3);
      return;
    }
    if (!priceReg.test(LooseObj.dealPrice) || Number(LooseObj.dealPrice) <= 0) {
      Toast.info(`${utitle}`, 2);
      return;
    }

    if (!this.setBuyingNumber(LooseObj.number)) {
      Toast.info(`${utitle}`, 2);
      return;
    }
    if (this.props.userName == "") {
      if (!this.isPositiveInteger(LooseObj.cntDesc)) {
        Toast.info("", 2);
        return;
      }
    }

    // if (this.SETNUmber(LooseObj.number, LooseObj.dealCnt, "2", LooseObj)) {
    // }

    let LooseArr = this.state.LooseArr;
    LooseArr.push({
      todoList: [
        {
          code: "",
          title: "",
          num: 0,
          Examples: "********",
        },
        {
          code: "",
          title: "",
          num: 9,
          Examples: "*******1",
        },
        {
          code: "",
          title: "",
          num: 99,
          Examples: "******01",
        },
        {
          code: "",
          title: "",
          num: 99,
          Examples: "********",
        },
        {
          code: "",
          title: "",
          num: 999,
          Examples: "********",
        },
        {
          code: "",
          title: "",
          num: 999,
          Examples: "*****001",
        },
        {
          code: "",
          title: "",
          num: 4999,
          Examples: "*****001",
        },
      ],
      dealCnt: "", //
      number: "",
      endnumber: "", //
      dealPrice: "",
      cntDesc: "1", //
      tag: "",
      Examples: "********",
    });
    this.setState({
      LooseArr: LooseArr,
    }); 
    setTimeout(() => {
      window.scrollTo(0, this.MyLoose_body.current.offsetHeight);
    }, 300);
  }

  render() {
    return (
      <div className="Loose">
        <div className="Loose_title">
          {/* <p>{this.props.utitle}</p> */}
        </div>
        <div className="Loose_body" ref={this.MyLoose_body}>
          {this.state.LooseArr.map((item, key) => (
            <ul key={key}>
              <div className="title_num_del">
                <div className="sanlain">
                  <span></span>
                  <span>{key + 1}</span>
                </div>
                {key > 0 ? (
                  <div onClick={() => this.delte(item, key)} className="deltes">
                    <img src={require("../../assets/delete.png")} alt="" />
                  </div>
                ) : null}
              </div>
              <li>
                <div>{this.props.userName}</div>
                <div className="select-area" data-tap-disabled="true">
                  <span>{item.tag}</span>
                  <img src={require("../../assets/right.png")} alt="" />
                  <div data-tap-disabled="true">
                    <select
                      onChange={(e) => this.selectChange(e, item, key)}
                      value={item.tag}
                    >
                      {/* <option></option> */}
                      {item.todoList.map((item1, key1) => (
                        <option
                          onClick={() => this.Myoption(item1)}
                          value={item1.code}
                          key={key1}
                        >
                          {item1.title}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </li>
              <li>
                <div className="peihaoHaoma">{this.props.userName}</div>
                <div className="peihaoInpnt">
                  <input
                    type="text"
                    value={item.number}
                    onFocus={() => this.inputOnFocus()}
                    onChange={(ev) => this.hanChange(ev, key, item)}
                    placeholder=""
                  />
                  <p>:{item.Examples}</p>
                </div>
              </li>
              <li>
                <div>:{item.number}</div>
                <div style={{ color: "#333333" }}>
                  :{item.endnumber}
                </div>
              </li>

              {this.props.userName == "" ? (
                <li>
                  <div></div>
                  <input
                    type="text"
                    value={item.cntDesc}
                    onChange={(ev) => this.hancntDescChange(ev, key)}
                    placeholder=""
                  />
                </li>
              ) : null}
              <li>
                <div></div>
                <input
                  type="text"
                  value={item.dealPrice}
                  onChange={(ev) => this.hanpriceChange(ev, key)}
                  placeholder=""
                />
              </li>
            </ul>
          ))}
          <button className="add" onClick={() => this.add()}>
            <img src={require("../../assets/looseadd.png")} alt="" />
            
          </button>
        </div>
      </div>
    );
  }
}
