import React from "react";
import axios from "../../axios/index";
import iconCheck from "../../assets/iconcheck.png";
import iconCheckbox from "../../assets/iconcheckbox.png";
import { Toast } from "antd-mobile";
import Uheader from "../../Goolbal/Uheader";

export default class SearchNumber extends React.Component {
  constructor(props) {
    super(props);
    //this.state = {}  
    this.state = {
      numberType: [
        "()",
        "()",
        "",
        "()",
        "()",
        "",
        "()",
        "()",
        "",
        "",
        "",
        "",
      ],
      searchName: "", //
      searchData: [], //
      searchHistory: [], //
      position: "end", //
      positionName: "", //
      sname: "", //String		
      name: "", //String			
      type: "1", //String		
      tag: "", //String		
      category: "", //
      unitName: "", //
    };
  }

  componentWillMount() {
    document.title = "";

    /** */
    if (this.getUrlParam("name")) {
      this.setState({
        searchName: this.getUrlParam("name"),
        name: this.getUrlParam("name"),
        category: this.getUrlParam("category"),
        unitName: this.getUrlParam("unitName"),
        searchData: [],
      });
      let searchInfo = {
        name: this.getUrlParam("name"),
        unitName: this.getUrlParam("unitName"),
        categoryName: this.getUrlParam("category"),
      };
      sessionStorage.setItem("searchInfo", JSON.stringify(searchInfo));
    }

    /** */
    let arr = sessionStorage.getItem("searchistory")
      ? JSON.parse(sessionStorage.getItem("searchistory"))
      : [];
    this.setState({
      searchHistory: arr,
    });
  }

  /** */
  getUrlParam = (name) => {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = this.props.location.search.substr(1).match(reg);
    if (r != null) {
      return decodeURI(r[2]);
    }
    return ""; //return;undefined
  };

  /** */
  searchNumProduct = (en) => {
    this.setState({
      searchName: en.target.value,
    });
    if (en.target.value === "") {
      this.setState({
        searchData: [],
      });
      return false;
    } //ref={input => this.input = input}
    axios
      .post("subject/json/searchNumProduct", {
        sname: en.target.value, //String			
        pageSize: "20", //	String		
        pageIndex: "1", //	String		
      })
      .then((res) => {
        this.setState({
          searchData: res.data.resultObject.dataList,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };

  /** */
  searchSname(en) {
    let reg = /[^\w/]/ig;
    if (reg.test(en.target.value)) {
      Toast.info('',2);
      return false
    }
    this.setState({
      sname: en.target.value.replace(/[^\w/]/ig,''),
    });
  }

  /* */
  checkName(name, category, unit) {
    this.setState({
      searchName: name,
      name: name,
      category: category,
      unitName: unit,
      searchData: [],
    });
    let searchInfo = {
      name: name,
      unitName: unit,
      categoryName: category,
    };
    sessionStorage.setItem("searchInfo", JSON.stringify(searchInfo));
  }

  /** */
  changePosition(posit) {
    this.setState({
      position: posit,
    });
    switch (posit) {
      case "end":
        this.setState({
          positionName: "",
        });
        break;
      case "start":
        this.setState({
          positionName: "",
        });
        break;
      case "any":
        this.setState({
          positionName: "",
        });
        break;
      default:
        break;
    }
  }

  /** */
  changeType(posit) {
    this.setState({
      type: posit,
    });
  }

  /** */
  changeTag(posit) {
    this.setState({
      tag: posit,
    });
  }

  /** */
  clearName(en) {
    this.setState({
      searchName: "",
      name: "",
      category: "",
      unitName: "",
      searchData: [],
    });
  }

  /* */
  searchNum() {
    if (this.state.searchName === "") {
      Toast.info("", 2);
      return false;
    }
    
    if (this.state.searchName !== "" && this.state.name === "") {
      Toast.info("", 2);
      return false;
    };
    

    /** */
    let obj = {
      name: this.state.name,
      sname: this.state.sname,
      tag: this.state.tag,
      type: this.state.type,
      position: this.state.position,
      positionName: this.state.positionName,
      category: this.state.category,
      unitName: this.state.unitName,
      url: this.getUrlParam("url")
    };
    let arr = this.state.searchHistory;
    arr.unshift(obj);
    if (arr.length > 5) {
      arr = arr.splice(0, 5);
    }
    sessionStorage.setItem("searchistory", JSON.stringify(arr));

    this.props.history.push(
      `/searchResult?name=${this.state.name}&type=${this.state.type}&sname=${this.state.sname}&position=${this.state.position}&tag=${this.state.tag}&category=${this.state.category}&unitName=${this.state.unitName}&url=${this.getUrlParam("url")}`
    );
  }

  /** */
  historySearch(item) {
    this.props.history.push(
        `/searchResult?name=${item.name}&type=${item.type}&sname=${item.sname}&position=${item.position}&tag=${item.tag}&category=${item.category}&unitName=${item.unitName}&url=${item.url}`
      );
  }

  /** */
  goMarketList() {
    let marketFrom = {
      url: this.getUrlParam("url"),
      market: "searchNumber",
    };
    sessionStorage.setItem("marketFrom", JSON.stringify(marketFrom));
    this.props.history.push("/catalogue");
  }

  render() {
    return (
      <div
        className="searchNumber"
        style={{
          background: "#ffffff",
          minHeight: "100%",
          display: "flex",
          flexDirection: "column",
        }}
      >
        <Uheader {...this.props} utitle=""></Uheader>
        <div className="searchTop">
          <button
            className={this.state.type === "1" ? "activeBtn" : ""}
            onClick={() => this.changeType("1")}
          >
            
          </button>
          <button
            className={this.state.type === "2" ? "activeBtn" : ""}
            onClick={() => this.changeType("2")}
          >
            
          </button>
          <div className="searchBox">
            <label htmlFor="name">  </label>
            <input
              type="text"
              name="name"
              placeholder=""
              value={this.state.searchName}
              onChange={this.searchNumProduct.bind(this)}
              onFocus={(en) => this.clearName(en)}
            />
            <span className="catalog" onClick={this.goMarketList.bind(this)}>
              
            </span>
          </div>
        </div>
        <div
          style={{
            height: "10px",
            background: "rgba(242,242,242,1)",
          }}
        ></div>
        <div className="mainCon">
          <div className="radiogrup">
            <dl onClick={() => this.changePosition("end")}>
              <dd>
                <img
                  src={this.state.position === "end" ? iconCheck : iconCheckbox}
                  alt=""
                />
              </dd>
              <dt>  </dt>
            </dl>
            <dl onClick={() => this.changePosition("any")}>
              <dd>
                <img
                  src={this.state.position === "any" ? iconCheck : iconCheckbox}
                  alt=""
                />
              </dd>
              <dt>  </dt>
            </dl>
            <dl onClick={() => this.changePosition("start")}>
              <dd>
                <img
                  src={
                    this.state.position === "start" ? iconCheck : iconCheckbox
                  }
                  alt=""
                />
              </dd>
              <dt>  </dt>
            </dl>
          </div>
          <div className="searchBox">
            <input
              type="text"
              placeholder=""
              value={this.state.sname}
              onChange={this.searchSname.bind(this)}
            />
            <button onClick={() => this.searchNum()}></button>
          </div>
          <div className="searchlist">
            <div className="searchtitle">  </div>
            <ul>
              {this.state.numberType.map((val,index) => (
                <li key={index}
                  className={
                    this.state.tag === val ? "activeBtn typelist" : "typelist"
                  }
                  onClick={() => this.changeTag(val)}
                >
                  {val}
                </li>
              ))}
            </ul>
          </div>
          {this.state.searchHistory.length > 0 ? (
            <div className="searchlist">
              <div className="searchtitle">  </div>
              {/* <div className="searchtitle"><span>()</span><img className="deleteIcon" src={require("../../assets/delete.png")} alt=""/></div> */}
              <ul>
                {this.state.searchHistory.map((item, index) => (
                  <li className="typelist" key={index} onClick={() =>this.historySearch(item)}>
                    {item.name} {item.sname ? `${item.positionName}` : ""}
                    {item.sname ? `${item.sname}` : ""}
                    {item.tag ? `${item.tag}` : ""}
                  </li>
                ))}
              </ul>
            </div>
          ) : null}
          {this.state.searchData.length > 0 ? (
            <div className="searchNum">
              {this.state.searchData.map((item, index) => (
                <p
                  key={index}
                  className="list"
                  onClick={() =>
                    this.checkName(item.name, item.category, item.unitName)
                  }
                >
                  {item.name}
                </p>
              ))}
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}
